body {
  background-color: #e9ebf0;
  font-family: Helvetica, Arial;
}

.wrapper {
  margin-top: 50px;
  background-color: #fff;
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  width:80%;
}
.wrapper p {
  font-style: italic;
  color: #666;
}

.wrapper label {
  display: block;
}
.wrapper input {
  padding: 3px 6px;
  width:300px;
}

.amount-input {
  margin-bottom: 10px;
}

.wrapper hr {
  margin: 40px auto;
}
