.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.convert-box {
  display:flex;
  flex-direction:row;
  justify-content: space-around;
}

.input-cell{
  width:65px;
}

input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }

#results_table{
  width:800px;
  overflow: visible;
  table-layout: fixed;
}

#results_table_div{
  width:1250px;
}

@media print {
  #print_button{
    display: none;
  }
  .pagebreak { page-break-before: always; }
}

.value{
  text-overflow:ellipsis;
  width:50px;
  overflow:hidden;
  white-space: nowrap;
}

.value:hover{
  overflow: visible;
}



 
 
 